import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';

import { NoteService } from '../../app/note.service';
import { AngularFireDatabase } from 'angularfire2/database';
import { AuthService } from '../../app/auth.service';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  notes;
  userId;

  constructor(public navCtrl: NavController, private noteService: NoteService, db: AngularFireDatabase,private authService: AuthService) {
    console.log(db);
  }

  ngOnInit(){
    this.authService.getCurrentUser().subscribe(authState => {
      this.userId = authState.uid;
      this.notes = this.noteService.fetchNotes(this.userId);
    });

    /*
      .then(res => {
        this.notes = this.noteService.notes;
      });
      */
    /*
    this.noteService.fetchNotes()
      .then(res=>{
        this.notes = this.noteService.notes;
      });
    */
  }

  logout(){
     console.log("logout");
     this.authService.logout();
     //this.navCtrl.setRoot('LoginPage');
   }
  onItemClick(note){
    this.navCtrl. push('DetailPage',{
      noteParam : note,
      userId: this.userId
    });
  }

  onAddClick(){
    this.navCtrl.push('DetailPage',{
      userId: this.userId
    }); // for add, don’t pass in any parameters.
  }
}

